
.arrow {
    font-size: 40px;
    text-align: center;
    cursor: pointer;
}
.arrow:hover {
    color: gold;
    transition: 0.2s;
}
.none {
    display: none;
}
.menu {
    display: flex;
    justify-content: center;
    gap: 15px;
}

.item {
    color: white;
}
.active {
    color: gold;
}
.error {
    text-align: center;
    color: darkred;
}